<template>
  <v-container>
    <h3>Creare conținut</h3>

    <v-row>
      <v-col cols="6">
        <v-file-input small-chips multiple label="Alege" ref="abcde" v-model="files"></v-file-input>
      </v-col>

      <v-col cols="6">

        <v-row>
          <v-checkbox
            v-model="splitByHeading"
            hide-details
            class="mr-2"
            label="Spargere pe prima categorie de titluri"
          />
        </v-row>
        <v-row>
          <v-checkbox disabled
            v-model="createCollection"
            hide-details
            class="shrink mr-2 mt-0"
          ></v-checkbox>
          <v-text-field
            v-model="collectionTitle"
            label="Creare sau adăugare la colecție"
          ></v-text-field>
        </v-row>

        <v-row>
          <v-combobox
             :items="literatureCategories"
             v-model="category"
             hide-details
             label="Categorie"
          />
        </v-row>
        <v-row>
          <v-select
            :items="accessLevels"
            v-model="listAccess"
            label="Acces listare"
          />
        </v-row>
        <v-row>
          <v-select
             :items="accessLevels"
             v-model="readAccess"
             label="Acces citire"
          />
        </v-row>

      </v-col>
    </v-row>
    <v-row>
      <v-btn @click="upload()" style="width: 75%; margin: auto;" >
        Dă-i bice
      </v-btn>
    </v-row>
  </v-container>
</template>

<script>
  import axios from 'axios';

  export default {
    name: 'LiteratureAdminUpload',

    data: () => ({

      // Form inputs
      files: [],
      splitByHeading: false,
      category: '',
      collectionTitle: '',
      listAccess: 'READER',
      readAccess: 'ADMIN'
    }),

    computed: {
      accessLevels () {
        return ['READER', 'USER', 'ADMIN'];
      },

      createCollection () {
        return !!this.collectionTitle;
      },

      literatureCategories () {
        // TODO This rarely used feature is now broken by the removal of the global
        return window.globals.literatureCategories;
      }
    },

    methods: {
      upload () {
        this.$emit('show-neither');

        if (this.files && this.files.length) {
          let formData = new FormData();

          // files
          for (let file of this.files) {
              formData.append("files", file, file.name);
          }

          // additional data
          formData.append("splitByHeading", this.splitByHeading);
          formData.append("category", this.category);
          formData.append("collectionTitle", this.collectionTitle);
          formData.append("listAccess", this.listAccess);
          formData.append("readAccess", this.readAccess);

          axios({ method: 'POST',
                  url: this.$apiUrl + '/l/upload',
                  data: formData,
                  maxRedirects: 0
          })
              .then(response => {
                  // TODO Show detail to user
                  this.$emit('show-ok');
              })
              .catch(error => {
                  this.$emit('show-error', error);
              });

        } else {
            this.$emit('show-error', 'Please select files.');
        }
      }
    }
  }

</script>
