<template>
  <v-container>
    <v-text-field v-model="category" label="Category" />
    <v-text-field v-model="collection" label="Collection" />
    <v-row>
      <v-col cols="3" />
      <v-col cols="6">
        <v-btn :disabled="!!whatsHappening" :loading="whatsHappening==='loading'" @click="load()" block> Load </v-btn>
      </v-col>
    </v-row>

    <v-textarea v-model="json" placeholder="Collection JSON" auto-grow outlined />
    <v-row>
      <v-col cols="3" />
      <v-col cols="6">
        <v-btn :disabled="!!whatsHappening" :loading="whatsHappening==='saving'"  @click="save()" block> Save </v-btn>
      </v-col>
    </v-row>

  </v-container>
</template>

<script>
  import axios from 'axios';

  export default {
    data: () => ({
      category: '',
      collection: '',
      json: '',

      whatsHappening: ''
    }),

    methods: {
      load() {
        this.whatsHappening = 'loading';
        this.$emit('show-neither');

        axios({ method: 'GET', url: this.$apiUrl + '/l/collection/' + encodeURIComponent(this.category) + '/' + encodeURIComponent(this.collection) })
              .then(result => { this.whatsHappening = ''; this.json = result.data.d; },
                    error  => { this.whatsHappening = ''; this.$emit('show-error', error); });
      },

      save() {
        this.whatsHappening = 'saving';
        this.$emit('show-neither');

        let formData = new FormData();
        formData.set('d', this.json);

        axios({ method: 'POST', url: this.$apiUrl + '/l/collection', data: formData })
              .then(result => { this.whatsHappening = ''; this.$emit('show-ok'); },
                    error  => { this.whatsHappening = ''; this.$emit('show-error', error); });
      }
    }
  }

</script>
