<template>
  <div>

    <v-tabs v-model="tab" grow @change="showNeither">
      <v-tab key="upload"> Upload entry files </v-tab>
      <v-tab key="edit"> Edit collections </v-tab>
    </v-tabs>

    <v-tabs-items v-model="tab">
      <v-tab-item key="upload">
        <LiteratureAdminUpload @show-error="showError" @show-ok="showOk" @show-neither="showNeither" />
      </v-tab-item>
      <v-tab-item key="edit">
        <LiteratureAdminEdit @show-error="showError" @show-ok="showOk" @show-neither="showNeither" />
      </v-tab-item>
    </v-tabs-items>

    <v-container>
      <v-alert v-model="okShown" type="success" border="right" color="#050" dismissible>
        OK
      </v-alert>
      <v-alert v-model="errorShown" type="error" border="right" color="purple" dismissible>
        {{errorMessage}}
      </v-alert>
    </v-container>

  </div>
</template>

<script>
  import LiteratureAdminUpload from '../components/LiteratureAdminUpload.vue';
  import LiteratureAdminEdit from '../components/LiteratureAdminEdit.vue';

  export default {
    components: {LiteratureAdminUpload, LiteratureAdminEdit},

    data: () => ({
      tab: '',

      errorMessage: 'Busted!',
      errorShown: false,
      okShown: false
    }),

    methods: {
      showError(msg) {
        this.errorMessage = msg.response && msg.response.data && msg.response.data.message || msg
        this.errorShown = true;
      },

      showOk() {
        this.okShown = true;
      },

      showNeither() {
        this.errorShown = false;
        this.okShown = false;
      }
    }
  }

</script>

<style>
  .v-alert {
    margin-top: 5px;
  }
</style>
